import React from 'react';
import {Button, InputGroup, Intent, FileInput} from '@blueprintjs/core';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {addCenter} from '../../../actions/CentersActions';
import {isFormValid} from "../../../helpers";
import Toaster from "../../Common/Toaster";
import {ADD_CENTER} from "../../../helpers/constants";
import _isEqual from "lodash/isEqual";

class NewMultipleCenters extends React.Component{

    state = {
        file: null
    };

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            if(OPERATION_SUCCESSFUL.action === ADD_CENTER){
                history.goBack();
                Toaster.show({
                    message: `Successfully Added 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            if(OPERATION_FAILED.action === ADD_CENTER){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Operation Failed 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState({[name]: value});
    };

    _handleAddCenter = () => {
        const {addCenter, authUser: {token}} = this.props;
        const {file} = this.state;

        let data = new FormData();

        data.append('multiple', file);

        addCenter(token, data)
    };

    _isFormValid(){
        const {name, code} = this.state;

        let required = {name, code};

        return isFormValid(required);
    }

    _handleDownloadTemplate = () => {}

    render(){
        const {addingCenter} = this.props;
        const {file} = this.state;
        const disableSaveButton = !this._isFormValid();

        return (
            <div className="">

                <label className="bp3-label">
                    Excel Sheet
                    <FileInput id={"sketch"}
                        className={"mm-input bp3-input-fill"}
                        text="Choose file..."
                        onInputChange={files => console.log(files)} />
                </label>

                <Button
                    disabled={disableSaveButton}
                    loading={addingCenter}
                    intent={Intent.PRIMARY}
                    onClick={this._handleAddCenter}
                    icon="tick"
                    text="upload"
                />

                <hr style={{ margin: '1rem 0', color: 'rgba(16,22,26,.15)'  }}/>

                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'spaceBetween', alignItems: 'center' }}>
                    <span style={{ marginBottom: '1rem' }}>OR</span>
                    <Button
                        disabled={addingCenter}
                        intent={Intent.NONE}
                        onClick={this._handleDownloadTemplate}
                        icon="download"
                        text="Download Centers Sheet Template"
                    />
                </div>
            </div>
        );
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        addCenter: PropTypes.func.isRequired,
        addingCenter: PropTypes.bool.isRequired,
    };
}

const mapStateToProps = (
    {OPERATION_SUCCESSFUL, OPERATION_FAILED, addingCenter, authUser}) => (
        {OPERATION_SUCCESSFUL, OPERATION_FAILED, addingCenter, authUser});

const mapDispatchToProps = dispatch => bindActionCreators({addCenter}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(NewMultipleCenters);


